html {
  font-size: 14px;
}
body {
  background-color: hsl(210, 60%, 98%);
  font-family: 'Plus Jakarta Sans', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 0.813rem;
  margin: 0;
}
* {
  box-sizing: border-box;
}
.box-main {
  padding: 20px 30px 10px 30px;
  border-radius: 15px;
  background-color: white;
  width: 600px;
}
.interface-main {
  height: 60px;
  display: flex;
  justify-content: space-between;
}
.number {
  margin-top: 0.125rem;
  font-weight: 500;
  display: flex;
  justify-content: center;
  padding-bottom: 0.125rem;
  text-align: center;
  align-items: center;
  font-size: 1rem;
  margin-left: 12px;
  color: white;
  height: 1.563rem;
  width: 1.875rem;
  border-radius: 6px;
  background-color: hsl(219, 85%, 26%);
}

.hint {
  display: flex;
  flex-direction: row;
  font-weight: 800;
  font-size: 1.5rem;
}
.clear {
  margin-top: 8px;
  user-select: none;
  cursor: pointer;
  color: hsl(219, 14%, 63%);
}
.clear:hover {
  color: hsl(219, 85%, 26%);
}
.notification {
  margin-bottom: 6px;
  background-color: white;
  display: flex;
  padding: 10px;
  min-height: 60px;
  justify-content: flex-start;
}
.notification:last-of-type {
  margin-bottom: 0;
}
.read {
  border-radius: 5px;
  background-color: hsl(210, 60%, 98%);
}

p {
  margin: 0;
}
.inline {
  display: inline;
}
.text-hint {
  border: 2px solid black;
}
img {
  height: 2.5rem;
  width: auto;
  object-fit: contain;
}
.photo-mean {
  right: 0;
  width: 2.5rem;
  height: auto;
}
.avatar {
  
}
.user-bold {
  font-weight: 800;
}
.info {
  width: 100%;
  margin-top: 2px;
  margin-left: 14px;
}
span {
  margin: 0 6px;
}
span:first-of-type {
  margin-left: 0;
}
span:last-of-type {
  margin-right: 0;
}
.gray {
  color: hsl(219, 12%, 42%);
}
.user-bold:hover {
  cursor: pointer;

  color: hsl(219, 85%, 26%);
}
.ago {
  color: hsl(219, 14%, 63%);
  font-weight: 0;
}
.red-point {
  margin-bottom: 1px;
  display: inline-block;
  margin-left: 5px;
  width: 7px;
  height: 7px;
  border-radius: 100%;
  background-color: hsl(1, 90%, 64%);
}
.blue {
  color: hsl(219, 85%, 26%);
}
.message {
  padding: 11px;
  margin-top: 10px;
  border-radius: 3px;
  color: hsl(219, 14%, 63%);
  border: 1px solid hsl(205, 33%, 90%);
  width: 100%;
  cursor: pointer;
  background-color: white;
}
.message:hover {
  --color1: hsl(211, 68%, 94%);
  border-color: var(--color1);
  background-color: var(--color1);
}

img {
  cursor: pointer;
}
@media (max-width: 600px) {
  .box-main {
    padding: 10px 10px;
    border-radius: 0;
    background-color: white;
    width: 100%;
  }
  body {
    height: auto;
  }
}
@media (max-width: 300px) {
  html {
    font-size: 12px;
  }
}
